import Helper from '@/function.js';
export default {

	data() {

		return {
			token:'',
			id:'',
			 bankName:'',//银行	
			subBankName:'',//开户行
			cardNo:'',//银行卡号
			accountName:'',//开户人
			array: ['支付宝'],
			// array: ['支付宝','中国工商银行', '中国农业银行', '中国建设银行', '中国邮政银行', '兴业银行', '中国农村商业银行', '浦发银行', '中兴银行', '中国银行', ],
			index: 0,
		}
	},
	onLoad:function(options){
		if (!Helper.isEmpty(options.item))
		{
			console.log(options.item)
			var item = JSON.parse(options.item);
			this.id = item.id;
			this.bankName = item.bankName;
			this.subBankName= item.subBankName;
			this.accountName= item.accountName;
			this.cardNo= item.cardNo;
			
			console.log(item.cardNo)
			for (var i = 0; i < array.length; i++) {
				if (array[i] == item.bankName)
				{
					this.index = i;
					break;
				}
			}
			
		}
	},
	onShow: function() {
		
		this.token = uni.getStorageSync('token');
		this.userInfo = uni.getStorageSync('userInfo');
		
	},

	methods: {
		/* 新增银行卡接口 */
		getAddbankAccount(){
			var _this=this		
			
			
			var params={
				id:this.id,
				bankName:_this.array[_this.index],//银行
				subBankName:_this.subBankName,//开户行
				cardNo:_this.cardNo,//银行卡号
				accountName:_this.accountName,//开户人
			
			}
			
			var url = _this.ApiGateWay + '/sale/Member/MemberAddBankAccount';
			var content = "添加成功"
			if (!Helper.isEmpty(this.id)){
				var url = _this.ApiGateWay + '/sale/Member/MemberEditBankAccount'
				content = "修改成功"
			}
			
			
			uni.request({				
				url: url,
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res );
					if (Helper.CheckData(res)) {
						uni.showModal({
							title:"信息提示",
							content:content,
							showCancel:false,
							success:function(res){
								uni.navigateBack({
									delta:1
								})
							}
						})			
						
					}
					else
					{
						uni.showModal({
							title:"出错了",
							content:res.data.errorMessage,
							showCancel:false,								
						})		
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}
				
				
				
			})
			
			
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index = e.target.value;
		},
		/* fanhui */
		bank() {
			uni.navigateBack({
				delta:1
			})


		},

		/* 性别选择 */
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index = e.target.value;
		},
		// showMulLinkageThreePicker() {
		// 	this.$refs.mpvueCityPicker.show();
		// },

		// onCancel(e) {
		// 	console.log(e);
		// },

	}

}
